.App {
  position: absolute;
  display: block;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0px;
  z-index: -1;
}

.blackout {
  height: 100vh;
  width: 100%;
  background-color: black;
}

img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  display: block;
}

.background-image {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-size: cover;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

.container {
  width: 100%;
  height: 100%;
}

.blurred {
  -webkit-filter: blur(25px);
          filter: blur(25px);
}



